body {
  font-size: 0.14rem;
}

.vaccine-app-bg {
  font-family: 'PingFangSC Regular', 'microsoft yahei';
}

.vaccine-app-bg .btn-submit {
  display: inline-block;
  box-sizing: border-box;
  height: 0.6rem;
  line-height: 0.6rem;
  background-color: #06cc7e;
  color: #fff;
  border-radius: 0.08rem;
  text-align: center;
  cursor: pointer;
  padding: 0 0.2rem;
  min-width: 2rem;
}

.vaccine-app-bg .zone {
  position: relative;
  width: 100%;
  margin-top: 0.2rem;
  padding: 0.3rem;
  box-sizing: border-box;
  background-color: #FFF;
  color: #333;
}

.vaccine-app-bg .inline-block {
  display: inline-block;
}

.vaccine-app-bg .icon-edit {
  display: inline-block;
  background: url(data:image/png;base64,) center no-repeat;
  background-size: 0.36rem 0.38rem;
  width: 0.4rem;
  height: 0.4rem;
}

.vaccine-app-bg .center {
  text-align: center;
}

.vaccine-app-bg .fr {
  float: right;
}

.vaccine-app-bg .fl {
  float: left;
}

.vaccine-app-bg a,
.vaccine-app-bg a:focus,
.vaccine-app-bg a:active {
  color: #06cc7e;
  text-decoration: none;
}

.vaccine-app-bg > .footer {
  position: absolute;
  bottom: 0;
  box-sizing: border-box;
  width: 100%;
  height: 1rem;
  border-top: 0.01rem solid #e6e6e6;
  background-color: #FFF;
  text-align: center;
  z-index: 10;
}

.vaccine-app-bg > .footer > li {
  display: inline-block;
  box-sizing: border-box;
  width: 50%;
  height: 100%;
  text-align: center;
}

.vaccine-app-bg > .footer > li > a {
  display: inline-block;
  width: auto;
}

.vaccine-app-bg > .footer > li > a .icon-img {
  display: inline-block;
  margin-top: 0.18rem;
  height: 0.38rem;
  width: 0.5rem;
  background-position: center;
  background-repeat: no-repeat;
}

.vaccine-app-bg > .footer > li > a .icon-img.clover {
  background-image: url(data:image/png;base64,);
  background-size: 0.36rem 0.36rem;
}

.vaccine-app-bg > .footer > li > a .icon-img.hat {
  background-image: url(data:image/png;base64,);
  background-size: 0.36rem 0.36rem;
}

.vaccine-app-bg > .footer > li > a span {
  display: block;
  font-size: 0.22rem;
  line-height: 0.35rem;
  color: #666;
}

.vaccine-app-bg > .footer > li > a.select .icon-img.clover {
  background-image: url(data:image/png;base64,);
}

.vaccine-app-bg > .footer > li > a.select .icon-img.hat {
  background-image: url(data:image/png;base64,);
}

.vaccine-app-bg > .footer > li > a.select span {
  color: #06cc7e;
}

.vaccine-app-bg > .footer > li:last-child {
  border-left: 0.01rem solid #e5e5e5;
}

.vaccine-app-bg > .footer.footer-enter {
  opacity: 0.01;
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
}

.vaccine-app-bg > .footer.footer-enter-active {
  opacity: 1;
  -webkit-transform: translateY(0%);
  transform: translateY(0%);
  -webkit-transition: all 500ms ease-in;
  transition: all 500ms ease-in;
}

.vaccine-app-bg > .header {
  position: absolute;
  width: 100%;
  height: 0.9rem;
  background-color: #06cc7e;
  font-size: 0.38rem;
  color: #FFF;
  line-height: 0.9rem;
  text-align: center;
  box-sizing: border-box;
  z-index: 10;
  letter-spacing: 0.02rem;
}

.vaccine-app-bg > .header.header-enter {
  opacity: 0.01;
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%);
}

.vaccine-app-bg > .header.header-enter-active {
  opacity: 1;
  -webkit-transform: translateY(0%);
  transform: translateY(0%);
  -webkit-transition: all 500ms ease-in;
  transition: all 500ms ease-in;
}

.vaccine-app-bg > .header .am-icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 0.9rem;
  height: 0.9rem;
}

.am-picker-popup-wrap {
  /*font-size: 60px;*/
}

.am-picker-popup-wrap .am-picker-popup-item {
  box-sizing: border-box;
  height: 1rem;
  font-size: 0.4rem;
}

.am-picker-popup-wrap .am-picker .am-picker-col {
  height: 6rem;
}

.am-picker-popup-wrap .am-picker .am-picker-col .am-picker-col-mask {
  height: 100%;
}

.am-picker-popup-wrap .am-picker .am-picker-col .am-picker-col-indicator {
  height: 0.4rem!important;
  line-height: 0.4rem!important;
  top: 50%!important;
  margin-top: -0.2rem;
}

.am-picker-popup-wrap .am-picker .am-picker-col .am-picker-col-content .am-picker-col-item {
  height: 0.4rem!important;
  line-height: 0.4rem!important;
  font-size: 0.3rem!important;
}

.am-modal-wrap .am-modal-transparent {
  width: 6rem;
}

.am-modal-wrap .am-modal-transparent .am-modal-content .am-modal-header .am-modal-title {
  font-size: 0.3rem;
  text-align: left;
  height: 0.3rem;
  line-height: 0.3rem;
}

.am-modal-wrap .am-modal-transparent .am-modal-content .am-modal-footer .am-modal-button-group-h .am-modal-button {
  height: 0.8rem;
  line-height: 0.8rem;
  font-size: 0.3rem;
}

.am-modal-wrap .am-modal-transparent .am-modal-content .am-modal-body .modal-wrapper .modal-row {
  width: 88%;
  float: left;
  padding: 0.15rem 0.4rem;
  color: #333;
  font-size: 0.3rem;
}

.am-modal-wrap .am-modal-transparent .am-modal-content .am-modal-body .modal-wrapper .modal-row .modal-title {
  float: left;
  height: 0.6rem;
  line-height: 0.6rem;
  font-size: 0.3rem;
}

.am-modal-wrap .am-modal-transparent .am-modal-content .am-modal-body .modal-wrapper .modal-row .modal-picker-list-item {
  height: 0.6rem;
  line-height: 0.6rem;
  float: left;
  border: 0.01rem solid #e5e5e5;
  width: 55%;
  border-radius: 0.04rem;
}

.am-modal-wrap .am-modal-transparent .am-modal-content .am-modal-body .modal-wrapper .modal-row .modal-picker-list-item .am-list-extra {
  text-align: left;
  overflow: visible;
}

.am-modal-wrap .am-modal-transparent .am-modal-content .am-modal-body .modal-wrapper .modal-row .modal-checkbox {
  float: left;
}

.am-modal-wrap .am-modal-transparent .am-modal-content .am-modal-body .modal-wrapper .modal-row .modal-checkbox .am-checkbox {
  width: 0.4rem;
  height: 0.4rem;
  margin-right: 0.15rem;
}

.am-modal-wrap .am-modal-transparent .am-modal-content .am-modal-body .modal-wrapper .modal-row .modal-checkbox .am-checkbox .am-checkbox-inner {
  width: 0.4rem;
  height: 0.4rem;
}

.am-modal-wrap .am-modal-transparent .am-modal-content .am-modal-body .modal-wrapper .modal-row .modal-checkbox .am-checkbox.am-checkbox-checked .am-checkbox-inner {
  border-color: #06cc7e;
  background: #06cc7e;
}

.am-modal-wrap .am-modal-transparent .am-modal-content .am-modal-body .modal-wrapper .modal-row .modal-checkbox .am-checkbox .am-checkbox-inner:after {
  top: 0.04rem;
  right: 0.1rem;
  width: 0.15rem;
  height: 0.2rem;
  border-width: 0 0.03rem 0.03rem 0;
}